// 교육 모듈 테마 import
@use './styles/theme' as *;

// Education Theme Wrapper - 교육 모듈 전체에 테마 적용
.edu-theme-wrapper {
  // 교육 모듈의 accent 색상을 보라색 계열로 오버라이드
  --accent-primary: #667eea;
  --accent-secondary: #764ba2;
  --accent-color: #667eea;
  --primary-color: #667eea;
  --primary-hover-color: #5568d3;
  
  // 다크 테마일 때
  &.dark-theme {
    --accent-primary: #818cf8;
    --accent-secondary: #a78bfa;
    --accent-color: #818cf8;
    --primary-color: #818cf8;
    --primary-hover-color: #6366f1;
  }
}

.edu-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  
  // iOS에서 스크롤 바운스 효과 제거
  overscroll-behavior-y: contain;
  -webkit-overflow-scrolling: touch;
  
  // iOS Safari에서 100vh 문제 해결
  @supports (-webkit-touch-callout: none) {
    height: -webkit-fill-available;
  }
}

.edu-content {
  min-height: 100%;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  position: relative;
  
  // iOS에서 스크롤 영역 명확히 설정
  touch-action: pan-y;
}